/* http://paletton.com/palette.php?uid=13w050k0-5-3vuNq3qsgz4rp63R */
.app{
  height: 100%;
}
main{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 90%;
  width: 100%;
  padding-bottom: 25px;
}
nav{
  text-align: center;
}
nav a{
  margin: 5px 15px;
  padding: 5px;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  border-bottom: 1px solid #555;
  font-size: 1.5vw;
}
nav a:hover {
  text-decoration:none; 
  color:#fff; 
  border-bottom: 1px solid #BA68C8;
}
.currentPage{
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
  border-bottom: 1px solid #d32f2f;
}


