.homeContainer{
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}
.homeHeading{
  color: #fff;
  font-size: 6vw;
  margin: 0;
  padding: 0;
  -webkit-animation: fade-in 2s ease-in;
          animation: fade-in 2s ease-in;
}
.homeStatement{
  color: #fff;
  font-size: 1.5vw;
  margin: 5px 0;
  padding: 0;
  -webkit-animation: fade-in 4s ease-in, change-colors 25s ease-in-out infinite;
          animation: fade-in 4s ease-in, change-colors 25s ease-in-out infinite;
}

@-webkit-keyframes fade-in{
  from{opacity: 0;}
  to{opacity: 1;}
}

@keyframes fade-in{
  from{opacity: 0;}
  to{opacity: 1;}
}

@-webkit-keyframes change-colors{
  0%{
    color: #1E88E5;
  }
  25%{
    color: #d32f2f;
  }
  50%{
    color: #43A047;
  }
  65%{
    color: #FFCA28;
  }
 80%{
  color: #BA68C8;
  }
  100%{
  color: #1E88E5;
  }
}

@keyframes change-colors{
  0%{
    color: #1E88E5;
  }
  25%{
    color: #d32f2f;
  }
  50%{
    color: #43A047;
  }
  65%{
    color: #FFCA28;
  }
 80%{
  color: #BA68C8;
  }
  100%{
  color: #1E88E5;
  }
}


