.arrowNavContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: start;
      justify-content: flex-start;
  min-height: 400px;
  min-width: 95px;
  margin-top: 15px;
}
.arrowNavLink{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-decoration: none;
  color: #fff;
  margin-bottom: 5px;
}
.arrowNavLink:hover {
  text-decoration:none; 
  color:#fff; 
}
.arrowNavLinkName{
  font-size: 1.2vw;
  border: 2px solid #BA68C8;
  border-right: none !important;
  border-radius: 10px 0px 0 10px;
  padding: 9px 10px;
  text-align: center;
  -webkit-transition: 0.6s ease-in-out;
  -o-transition: 0.6s ease-in-out;
  transition: 0.6s ease-in-out;
  position: relative;
  z-index: 1;
}

.arrowNavLinkName::before{
  content: '';
  background: #BA68C8;
  width: 0;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: all 0.7s ease-in-out;
  -o-transition: all 0.7s ease-in-out;
  transition: all 0.7s ease-in-out;
  border-radius: 8px 0px 0 8px;
  opacity: 0;
  z-index: -1;
}
.arrowNavLinkName:hover::before{
  width: 100%;
  opacity: 1;
}


