  border-radius: 6px;
  border: 1px solid #1E88E5;
}
.projectDesc{
  padding: 0 10px;
  margin: 5px;
  text-align: center;
  font-size: 1.2vw;
}
.projectTechnologies{
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 8px;
}
.projectTech{
  padding: 6px;
  margin: 6px;
  border-radius: 10px;
  -webkit-box-shadow: 3px 3px 10px -6px rgba(0,0,0,0.5);
          box-shadow: 3px 3px 10px -6px rgba(0,0,0,0.5);
  font-size: 1vw;
  text-align: center;
}
.projectButtons{
  margin: 15px;
}
.projectButton{
  margin: 0 10px;
  border-bottom: 1px solid #d32f2f;
  font-size: 1.5vw;
  text-decoration: none;
  color: #555;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.projectButton:hover{
  text-decoration:none; 
  color:#555; 
  border-bottom: 1px solid #1E88E5;
}


